import React from 'react'
import { ReactElement } from 'react'

import { SubjectSummary } from '../../../components/subject/SubjectSummary'
import { SubjectContent } from '../../../components/subject/SubjectContent'
import { SubjectTitle } from '../../../components/subject/SubjectTitle'
import { SubjectKeyword } from '../../../components/subject/SubjectKeyword'
import { SubjectIdentifier } from '../../../components/subject/SubjectIdentifier'
import { Subject } from '../../../components/subject/Subject'

export function Fatalities () : ReactElement {
  return (
    <Subject>
      <SubjectIdentifier>rules-sanity-fatalities</SubjectIdentifier>
      <SubjectKeyword>règle</SubjectKeyword>
      <SubjectTitle>Fatalités</SubjectTitle>
      <SubjectSummary>

      </SubjectSummary>
      <SubjectContent>
        <p>
          Vivre, c'est attendre la mort, et nul ne peut courrir assez vite pour
          échapper à cette destinée, alors à quoi bon courrir ? C'est l'essence
          même de la fatalité et différents éléments du récit peuvent mettre les
          personnages, même les plus endurcis d'entre-eux, face à leur condition
          précaire et les ammener à réviser leurs chances de survie à court
          terme.
        </p>

        <p>
          <em>Dégâts.</em> Être victime d'un coup violent à tendance à remettre
          les idées en places et à reconsidérer notre estimation initiale des
          chances de sortir vivant des affres du combat en cours. Quand un
          personnage subit des dégâts, il doit réaliser un test de volonté d'un
          degré de difficulté équivalent au nombre de points de dégâts subits.
          Si le test réussi, le personnage ne perd qu'un point de volonté, s'il
          échoue, le personnage perd 1D4 points de volonté.
        </p>

        <p>
          <em>Blessure.</em> Quand un personnage est blessé au cours d'un
          combat, le degré de difficulté du test de volonté qu'il doit subir
          augmente de 5 points si la blessure est légère et de dix points si
          la blessure est grave. En cas de succès il perd un point de volonté
          supplémentaire à cause de la blessure, en cas d'échec le nombre de
          points de volontés perdu monte à 1D6 + 1 en place des 1D4 usuels.
        </p>

        <p>
          <em>Coma.</em> La chute dans le coma d'un personnage c'est aussi un
          coup supplémentaire asséner à l'esprit de tous ses alliés. Quand un
          personnage tombe au combat l'ensemble de ses alliés doit réussir un
          test de volonté d'un degré de difficulté de 10 points. Si le test
          réussi, le personnage ne perd que 2 points de volonté, sinon il perd
          1D4 + 1 points.
        </p>

        <p>
          <em>Mort.</em> Le décès d'un personnage est encore plus traumatisant
          encore qu'un simple coma. Quand un personnage meurt au combat
          l'ensemble de ses alliés doit réussir un test de volonté d'un degré de
          difficulté de 15 points. Si le test réussi, le personnage ne perd que
          2 points de volonté, sinon il perd 1D6 + 1 points.
        </p>

        <p>
          <em>Échec critique.</em> Faire face à l'échec est une épreuve en soit
          et peut ammener certains esprits à remettre en cause l'étendue réelle
          de leur compétence. Quand un personnage est sujet à un échec critique
          il doit réussir un test de volonté d'un degré de difficulté de 10
          points. Si le test réussi, le personnage ne perd qu'un point de
          volonté, sinon il perd 1D4 points.
        </p>

        <p>
          <em>Surnombre.</em> Faire face à plus d'adversaires que de raison peut
          amener à raisonablement penser que la partie est mal engagée. En cas
          de conflit, si les adversaires sont en surnombre, les joueurs doivent
          réaliser un test de volonté d'un degré de difficulté équivalent au
          nombre d'adversaire supplémentaire multiplié par 2. Si le test réussi,
          le personnage ne perd qu'un point de volonté, sinon, il en perd 1D4.
        </p>
      </SubjectContent>
    </Subject>
  )
}
